$img_path:"../../img/";
$black:#333333;
 @function px2vw($size, $std_width: 390) {
   $no_unit_size: calc($size / ($size * 0 + 1));
   @return calc(100 / $std_width) * $no_unit_size * 1vw;
 }
 @function px2pc($size,$std_width: 1440){
   $no_unit_size: calc($size / ($size * 0 + 1));
   @return calc(100 / $std_width) * $no_unit_size * 1vw;
 }
 // SPデザインPC変換
@function dpx($size, $dpx_width: 390px, $dpx_resize: 520px) {
   $dpx_calc: calc($dpx_width / $dpx_resize);
   @return round(calc($size / $dpx_calc) * 1px);
}
// break point
// デフォルトはmdでレイアウト切り替え
$breakpoints: (
  'sm': (max-width: 767px),
  'md': (min-width: 768px),
  'lg': (min-width: 980px),
  'xl': (min-width: 1200px),
  'xxl': (min-width: 1300px),
  'pd': (max-width: 1300px),
  'pd2': (max-width: 1180px),
  'pd3': (max-width: 991px),
  'sm_landscape' : '(max-width: 812px) and (max-height: 576px) and (orientation: landscape)',
  'sm_tall' : '(max-width: 539px) and (min-height: 700px) and (max-height: 1024px) and (orientation: portrait)' ,
  'md_portrait' : '(min-width: 540px) and (max-width: 768px) and (max-height: 1024px) and (orientation: portrait)',
  'ie11' :  '(min-width: 768px) and (-ms-high-contrast: none)'
) !default;

/* --------------------------------------------------------------------------
  media query (SP（小）→ PC（大）の順に適用)
  -------------------------------------------------------------------------- */
@mixin mq($breakpoint) {
  @if map-has-key($breakpoints, $breakpoint) {
    @media screen and #{inspect(map-get($breakpoints, $breakpoint))} {
      @content;
    }
  }
  @else {
    @error "指定されたブレークポイントは定義されていません。" + "指定できるブレークポイントは次のとおりです。 -> #{map-keys($breakpoints)}";
  }
}
@media screen and (max-width: 767px) {
   .sample{
      font-size:px2vw(21);
   }
}
@mixin opacity(){
   transition:all 0.3s;
   &:hover{
      opacity:0.6;
   }
}
@mixin iconset(){
   display: block;
   content:"";
   position:absolute;
   background-size: contain;
   background-repeat: no-repeat;
}

img{
   width:100%;
}
body{
   font-family:"Noto Sans JP", sans-serif;
   font-feature-settings:"palt";
}
.pc{
   display:block;
}
.sp{
   display:none;
}